@range-width: 33%;
@range-height: 12%;
@range-font-size: 1.6em;

.remaining-range {
    width: @range-width;
    height: @range-height;
    outline: 1px solid #FFA988;
    position: absolute;
    background: url('../images/arrow.png') 10% 50% no-repeat;
    background-color: #FFF;

    .range {
        position: relative;
        width: 60%;
        height: 80%;
        display: block;
        background: url('../images/gas-station.png') 5% 30% no-repeat;
        background-size: 30%;
        font-size: @range-font-size;
        line-height: @range-font-size;
        font-family: Monospace;
        padding-top: 2em - @range-font-size;
        padding-right: 5%;
        text-align: right;
        left: 35%;
        top: 10%;

        &:after {
            content: 'REMAINING RANGE';
            position: absolute;
            font-size: @range-font-size / 3;
            font-family: Monospace;
            bottom: 0;
            right: 5%;
            width: 100%;
            text-align: right;
        }
    }
}

.solid .remaining-range {
    bottom: 4%;
    right: 4%;
}

#route .remaining-range {
    bottom: 100px;
    left: 20px;
    width: 250px;
    height: 100px;
}