@side-nav-left-border-size: 0.7em;

.side-nav {
    background-color: #2b2b2b;
    position: fixed;
    top: @header-height;
    left: 0;
    width: @sidebar-width;
    height: 1000px;

    li {
        background-repeat: no-repeat;
        background-position: 55% 25%;
        filter: grayscale(1); /* Firefox 35+ */
        -webkit-filter: grayscale(100%);

        &.route{
            background-image: url('../images/section-route.png');
        }

        &.poi {
            background-image: url('../images/section-poi.png');
        }

        &.user-profiles {
            background-image: url('../images/section-user-profiles.png');
        }

        &.fuel-stop {
            background-image: url('../images/section-fuel-stop.png');
        }

        &.active {
            .highlighted-menu-item;

            a {
                color: @orange;
                border-left: @side-nav-left-border-size solid @orange;
                padding-left: 0px;
                text-decoration: none;

                &:after{
                    content: "";
                    width: 0;
                    height: 0;
                    border-top: 1em solid transparent;
                    border-bottom: 1em solid transparent;
                    border-right: 1em solid #FBFBFB;
                    position: absolute;
                    right: 0;
                    top: 35%;
                    display: block;
                }
            }
        }

        &:hover {
            .highlighted-menu-item;
        }

        a {
            font-size: 1.5em;
            width: 100%;
            height: 100%;
            padding-top: 3.5em;
            padding-bottom: 1em;
            text-align: center;
            font-weight: bold;
            display: block;
            color: #999;
            cursor: pointer;
            text-decoration: none;

            position: relative;
            padding-left: @side-nav-left-border-size;

            &:hover {
                color: @orange;
                background-color: transparent;
            }
        }
    }
}