@sidebar-width: 150px;
@header-height: 80px;
@orange: #FDB195;

@import "navigation";
@import "range";
@import "users";

body, div, span, a, ol, ul, nav, section, header, footer, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  border: 0;
  padding: 0;
  vertical-align: baseline;
}

html, body {
    height: 100vh;
    overflow: hidden;
}

ol, ul {
  list-style: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

a:hover {
  text-decoration: none;
}
@font-face {
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/AkkuratStd-Regular.otf") format('opentype');
  font-family: Akkurat;
}

@font-face {
  font-style: normal;
  font-weight: bold;
  src: url("../fonts/AkkuratStd-Bold.otf") format('opentype');
  font-family: Akkurat;
}

@font-face {
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/AkkuratMonoStd.otf") format('opentype');
  font-family: Akkurat Mono;
}

body {
  font-family: Akkurat, Helvetica Neue, Helvetica, Arial, Verdana, sans-serif;
}

.box, .box-bar, label {
  font-family: Akkurat Mono, Courier;
}

h1 {
  font-size: 4em;
}

h2 {
  font-size: 3em;
}

h3 {
  font-size: 2.2em;
}

h4 {
  font-size: 1.8em;
}

h5 {
  font-size: 1.4em;
}

h6 {
  font-size: 1em;
}

.hdg1 {
  font-size: 2em;
}

.l1 {
  font-size: 1.75em;
}

.l1 input {
  height: 1.75em;
  font-size: 1em;
}

.l2 {
  font-size: 1.5em;
}

.l2 input {
  height: 1.5em;
  font-size: 1em;
}

.l3 {
  font-size: 1em;
}

.l3 input {
  height: 1.5em;
  font-size: 1em;
}

body {
  color: #202020;
  background-color: #fff;
}

.page-title {
  color: #f0f0f0;
}

.top-nav {
  color: #fff;
  background-color: #ffb393;
}

.nav .dropdown-menu {
  border-radius: 0;
  border-color: #fff;
  background-color: #a7e6e0;
  color: #000;
}

.loading {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url('../images/loader.gif') center 30% no-repeat;
    background-size: 10%;
}

.nav .dropdown-menu >li>a:hover {
  background-color: #a7e6e0;
  color: #fff;
}

.highlighted-menu-item {
    background-color: #3b3b3b;
    filter: grayscale(0); /* Firefox 35+ */
    -webkit-filter: grayscale(0%);
}

.heading .info {
  color: #bababa;
}

form label {
    font-size: 1.8em;
    font-family: Akkurat;
    width: 100%;
}

form input {
    width: 100%;
    font-size: 1.8em;
    font-family: Akkurat
}

form#route label {
    width: auto;
}

form#route input[type="text"] {
    margin-left: 5px;
    height: 2em;
    width: 8em;
}

#settings form label {
    font-size: 1.3em;
}

#settings form input[type="text"] {
    font-size: 1.5em;
    border: 1px solid black;
    height: 1.9em;
    margin: 0;
}

#settings form input[type="submit"] {
    height: 2.7em;
}

#main {
    left: @sidebar-width;
    top: @header-height;
    bottom: 0px;
    position: fixed;
    width: 100%;
    height: 100%;
}

#main #route #directions {
    position: absolute;
    background: #FBFBFB;
    z-index: 100;
    padding: 20px;
    top: 20px;
    left: 20px;
    min-width: 30%;
}

#main #route #directions h2 {
    margin: 0;
    margin-bottom: 15px;
}

#main #route .remaining-range {
    z-index: 100;
}

#route ~ #map {
    width: 100%;
}

.solid ~ #map {
    width: 60%;
}

#map {
    float: right;
    height: 100%;
    z-index: 50;
}

#map ~ .solid {
    width: 40%;
}

.solid {
    min-width: 40%;
    height: 100%;
    float: left;
    background: #FBFBFB;
    padding: 10px 20px 0 20px;
}

.solid >div h2 {
    margin-top: 0.3em;
    margin-bottom: 0.3em;
}

.solid >div .info {
    margin-bottom: 0.9em;
    font-size: 1.5em;
}

.box:hover, .box.active {
  outline: none;
  box-shadow: 0 0 10px #a7e6e0;
  border-style: solid;
  border-color: hsla(174.28572, 55.752213%, 77.84314%, 0.8);
}

.box-bar {
  color: #a7e6e0;
  background-color: #000;
}

.inverse {
  color: #fff;
  background-color: #8e8e8e;
}

.inverse .a {
  color: #a7e6e0;
}

textarea:focus, input[type="text"]:focus, input[type="password"]:focus, input[type="datetime"]:focus, input[type="number"]:focus, input[type="email"]:focus, input[type="tel"]:focus, input[type="url"]:focus, input[type="search"]:focus {
  border-color: hsla(174.28572, 55.752213%, 77.84314%, 0.8);
}

.btn-primary {
  color: #000;
  border-color: #a7e6e0;
  background-color: #a7e6e0;
}

.btn-primary:hover {
  color: #000;
  border-color: #a7e6e0;
  background-color: #a7e6e0;
}

.top-user-greeting {
    padding-left: @header-height * 1.3;
    font-size: 1.5em;
    line-height: @header-height;
}

.top-user-name {
    color: #000
}

.top-logo {
  background-color: #ffa379;
  background-image: url("../images/ats-logo.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 75%;
  vertical-align: top;
  height: @header-height;
  float:left;
  width: @sidebar-width;

  a {
      width: 100%;
      height: 100%;
      display: block;
  }
}

.top-icon {
  background-image: url("../images/auth-icon.png");
  background-repeat: no-repeat;
  background-position: 15% 50%;
  background-size: contain;
}

.top-user-icon {
  background-image: url("../images/user-icon.png");
  background-repeat: no-repeat;
  background-position: 15% 50%;
  background-size: 27%;
  height: 40px;
}

.info-icon {
  background-image: url("../images/info-icon.png");
  background-repeat: no-repeat;
  background-position: 0% 0%;
}

.play-icon {
  background-image: url("../images/play-icon.png");
  background-repeat: no-repeat;
  background-position: 0% 0%;
}

.login-bg {
  background-image: url("../images/login-bg.jpg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
}

.page {
    margin-top: 10vh;
}

.page-title {
  padding-left: 230px;
  margin-left: 10px;
  margin-top: 20px;
}

.top-icon {
  vertical-align: top;
  display: inline-block;
  padding-left: 80px;
  border-right: 1px solid #ffa379;
  height: 48px;
  width: 50px;
}

.top-icon label {
  vertical-align: middle;
  line-height: 16px;
}

.top-user-icon {
  border-left: 1px solid #ffa379;
  height: @header-height;
  width: 230px;
  display: inline-block;
  float: right;
}

.top-user-icon label {
  vertical-align: middle;
  line-height: 52px;
  padding-right: 10px;
  padding-left: 80px;
}

.top-nav {
    min-height: 0px;
    height: @header-height;
    -webkit-transition: background-color 0.3s;
    transition: background-color 0.3s;
}
#poi ul {
    position: relative;
    height: 100%;
}

#fuel-stop ul .loading {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url('../images/loader.gif') center 30% no-repeat;
}


#fuel-stop table {
  background-image: url('../images/fuel-vertical-line.png');
  background-repeat: no-repeat;
  background-position: 39% 45px;
}

#fuel-stop span {
    font-size: 2em;
}

#fuel-stop .details {
    padding-left: 1.8em;
}

#fuel-stop .eta {
    font-family: Monospace;
    font-size: 2.2em;
    padding-right: 0.5em;
}

#fuel-stop .name {
    font-weight: bold;
}

#fuel-stop .brand {
    font-weight: bold;
}

#fuel-stop .price {
    font-family: Monospace;
    font-size: 1.9em;
}

#fuel-stop tr {
    height: 130px;
}

#fuel-stop td {
    line-height: 2.2em;
}

#fuel-stop td.icon {
    text-align: center;
    width: 2em;
    height: 2em;
    background-image: url('../images/waypoint.png');
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 100%;
    color: #FFF;
    /* Align the text to the centre of the circle. */
    font-size: 3em;
    padding: 3px 0 0 1px;
}

#fuel-stop td.destination {
    background-image: url('../images/waypoint-final.png');
}

#fuel-stop td.fill {
  height: 100px;
}

#poi ul {
    padding-top: 20px;
}
#poi li {
    line-height: 2em;
    font-size: 2em;
    padding: 10px 0 10px 70px;
    background-position: 0 50%;
    background-size: 10%;
    background-repeat: no-repeat;
}

#poi li.bmw {
    background-image: url('../images/poi-icon-bmw.png');
}

#poi li.mercedes {
    background-image: url('../images/poi-icon-mercedes.png');
}

#poi li.parking {
    background-image: url('../images/poi-icon-parking.png');
}

#poi li.cafe {
    background-image: url('../images/poi-icon-cafe.png');
}

#poi li.fuel {
    background-image: url('../images/poi-icon-fuel.png');
}

form input[type="button"], form input[type="submit"], form button {
    background-color: transparent;
    border: 2px solid #333;
    text-align: center;
    color: #333;
    margin: 0px auto;
    font-size: 1.6em;
    padding: 4px 0px;
}

form button {
    display: block;
    width: 100%;
}

form input[type="submit"], form button {
    margin: 1ex 0;
    background-color: #333;
    color: #EEE;
}

.heading {
  padding-bottom: 30px;
}

.heading .hdg1 {
  padding-right: 10px;
  display: inline-block;
  vertical-align: top;
}

.heading .info {
  text-indent: 30px;
  max-width: 500px;
}

.heading i {
  vertical-align: middle;
  line-height: 2em;
}

h1 {
  margin-top: 30px;
}

h2 {
  margin-top: 20px;
}

h3 {
  margin-top: 15px;
}

h4 {
  margin-top: 15px;
}

h5 {
  margin-top: 10px;
}

h6 {
  margin-top: 10px;
}

form {
  margin: 0px;
}

.box {
  padding: 10px;
  box-shadow: -2px 1px 10px -2px #EEE;
}

.box:hover {
  border-width: 0;
}

.box.active {
  border-width: 1px;
}

.box.frame {
  padding: 0;
  border-width: 0;
}

.box-0 {
  width: 300px;
}

.box-0 .boxbar {
  width: 300px;
}

.box-1 {
  width: 450px;
}

.box-1 .boxbar {
  width: 450px;
}

.box-2 {
  width: 600px;
}

.box-2 .boxbar {
  width: 600px;
}

.box-frame-2 {
  height: 600px;
  width: 600px;
}

.box-frame-2 .boxbar {
  width: 600px;
}

.box-bar {
  height: 34px;
}

.box-bar label {
  vertical-align: middle;
  line-height: 34px;
  padding-left: 10px;
}

.box-center {
  text-align: center;
  vertical-align: middle;
  line-height: 100%;
  height: 100%;
  margin-right: auto;
  margin-left: auto;
  width: 450px;
}

.box-center form {
  text-align: left;
}

.stamp {
  cursor: pointer;
  padding: 10px;
  box-shadow: 3px 3px 3px #888888;
  border-radius: 4px;
}

.stamp.down, .stamp:hover {
  box-shadow: none;
}

.stamp h1, .stamp h2, .stamp h3, .stamp h4, .stamp h5, .stamp h6 {
  text-shadow: 0 0 5px rgba(0,0,0,.2),0 0 2px #000;
  cursor: pointer;
  font-weight: 700;
  white-space: nowrap;
  color: #fff;
  text-overflow: ellipsis;
}

.menu-popup {
  position: fixed;
  z-index: 20;
}

i {
  padding-right: 10px;
}

input[type="text"], input[type="password"] {
  height: 30px;
  border-radius: 0;
}

.btn {
  height: 50px;
  width: 100%;
  border-radius: 0;
}

.m-item:hover {
  background-color: #3b3b3b;
}

.m-item:hover .m-item-tab {
  width: 6px;
  height: 40px;
  float: left;
  background-color: #ffb393;
}

.m-item-active {
  background-color: #3b3b3b;
}

.m-item-active:before {
  margin-top: 5px;
  border-style: solid;
  border-color: transparent #fff;
  border-width: 15px 10px 15px 0;
  visibility: visible;
  float: right;
  content: "";
}

.m-item-active .m-item-tab {
  width: 6px;
  height: 40px;
  float: left;
  background-color: #ffa379;
}
