@font-stack:    Helvetica, sans-serif;
@primary-color: #333;
@light-gray: #DDD;

@import "legacy";

.side-nav .updates {
    position: relative;
    a {
      font-size: 1.5em;
    }
}

.side-nav .section-badge {
    position: absolute;
    color: #FFF;
    font-size: 2.5em;
    top: 10%;
    text-align: center;
    width: 100%;
    left: 0;
    color: #BBB;
}

#updates {
    overflow: scroll;
    h4 {
        margin-bottom: 20px;
        font-size: 2.5em;
    }
    .software-update {
        &:first {
            margin-top: 0;
        }
        list-style: none;
        margin-top: 10px;
        padding: 25px;
        border: 1px double @light-gray;
        background-color: white;
        position: relative;

        div, button {
            position: absolute;
            right: 30px;
        }
        div {
            &.installed {
                background-image: url("../images/check.png");
                background-repeat: no-repeat;
                background-position: 50% 30%;
                background-size: 50%;
            }
            top: 15%;
            width: 100px;
            height: 100px;
            &.loading {
                background-image: url("../images/loader.gif");
                background-size: 50%;
            }
            span {
                font-size: 1.2em;
                position: absolute;
                bottom: 0px;
                text-align: center;
                width: 100%;
            }
        }
        button {
            top: 30%;
            padding: 15px;
            font-size: 1.6em;
            line-height: 1.6em;
        }
        dl {
            margin: 0;
            font-size: 1.6em;
        }
        dt {
            float: left;
            clear: left;
            width: 20%;
            text-align: right;
            font-weight: bold;
            color: dark-gray;
        }
        dt:after {
            content: ":";
        }
        dd {
            padding-left: 22%;
            padding-bottom: 0.2em;
        }
    }
    button {
        &.disabled {
            background-color: #AAA;
        }
        background-color: #333;
        color: #DDD;
        display: block;
        padding: 15px 25px;
        border: 0;
        font-size: 1.8em;
        text-transform: uppercase;
        margin: 0px auto;
    }
}