@user-height: 6.5em;

.button-mixin {
    position: absolute;
    bottom: 20px;
    margin: 0px auto;
    font-size: 1.7em;
    display: block;
    background-color: transparent;
    border: 2px solid #333;
    padding: 1em;
    width: 40%;
    text-align: center;
    color: #333;
}

.dark-button {
    background-color: #333;
    color: #DDD;
}

.login-input {
    width: 100%;
    font-size: 5em;
    text-align: center;
    
}

.user-form {
    a {
        .button-mixin;

        &.back {
            left: 6%;
        }
        &.login {
            .dark-button;
            right: 6%;
        }
    }

    input {
        .login-input;
    }
}

.users {
    .user-mixin {
        width: 100%;
        height: @user-height;
        margin-bottom: 1.3em;
    }

    .user {
        .user-mixin;
        background-color: #FFF;
        position: relative;
    }

    .user a.login {
        display: block;
        width: 80%;
        height: 100%;
    }

    .user .avatar {
        position: absolute;
        width: @header-height * 0.9;
        left: 1.7em;
        top: 7%;
        height: @header-height * 0.9;
        border-radius: 30px;
    }

    .user .name {
        position: absolute;
        font-size: 3em;
        font-weight: bold;
        left: 3em;
        line-height: @user-height / 3;
        color: #8A8A89;
    }

    .user.active {
        border: 1px solid #FFA989;
    }

    .user.active .name{
        color: #FFA989;
    }

    .user-action-mixin {
        background-repeat: no-repeat;
        background-position: 50% 50%;
        background-size: 90%;
        display: block;
        width: @header-height * 0.8;
        height: @header-height * 0.8;
        position: absolute;
        top: 15%;
        right: 5%;
    }

    .user.active:after{
        .user-action-mixin;
        content: "";
        background-image: url("../images/check.png");
    }

    .user.inactive .delete {
        .user-action-mixin;
        background-image: url("../images/cross.png");
    }

    .new-user {
        .user-mixin;
        border: 3px dashed #999;
        position: relative;
        background-color: transparent;
    }

    .new-user a {
        display: block;
        width: 100%;
        line-height: @user-height / 3 - 0.2em;
        font-size: 3em;
        font-weight: bold;
        color: #999;
        text-align: center;
    }
}

.login-form {
    input {
        .login-input;
    }

    .error {
        font-size: 1.8em;
        text-align: center;
        margin-top: 1.2em;
        color: #A00;
    }

    a.back {
        .button-mixin;
        left: 6%;
    }
}

